import React, { useEffect, useState } from 'react';
import './GlitchOverlay.css';

const GlitchOverlay = ({ isActive, audioRef }) => {
  const [glitchIntensity, setGlitchIntensity] = useState(0);
  const [scanlineOffset, setScanlineOffset] = useState(0);
  const [diamonds, setDiamonds] = useState([]);

  // Generate perfect sparkle diamonds (KEEP EXACTLY!)
  useEffect(() => {
    if (isActive) {
      const generateDiamonds = () => {
        const newDiamonds = [];
        for (let i = 0; i < 15; i++) { // More diamonds!
          newDiamonds.push({
            id: i,
            x: Math.random() * 100,
            y: Math.random() * 100,
            size: 0.8 + Math.random() * 0.6,
            delay: Math.random() * 3,
            duration: 3 + Math.random() * 2,
            type: Math.floor(Math.random() * 3),
          });
        }
        setDiamonds(newDiamonds);
      };

      generateDiamonds();
    } else {
      setDiamonds([]);
    }
  }, [isActive]);

  // Intense glitch effects
  useEffect(() => {
    let animationFrame;
    
    if (isActive && audioRef?.current) {
      const updateGlitch = () => {
        const time = Date.now() * 0.005;
        const pulse = Math.sin(time) * 0.5 + 0.5;
        const randomSpike = Math.random() > 0.95 ? Math.random() : 0; // More frequent spikes
        
        setGlitchIntensity(pulse * 0.6 + randomSpike * 0.9); // Much higher intensity
        setScanlineOffset((time * 80) % 100); // Faster scanlines
        
        animationFrame = requestAnimationFrame(updateGlitch);
      };
      
      updateGlitch();
    } else {
      setGlitchIntensity(0);
      setScanlineOffset(0);
    }

    return () => {
      if (animationFrame) cancelAnimationFrame(animationFrame);
    };
  }, [isActive, audioRef]);

  if (!isActive) return null;

  return (
    <div className="glitch-overlay">
      {/* CRT Scanlines - ENHANCED! */}
      <div 
        className="crt-scanlines"
        style={{
          transform: `translateY(${scanlineOffset}px)`,
          opacity: 0.4 + glitchIntensity * 0.2 // Much more visible
        }}
      />
      
      {/* Screen Static - INTENSE! */}
      <div 
        className="screen-static"
        style={{
          opacity: 0.15 + glitchIntensity * 0.1,
          filter: `hue-rotate(${glitchIntensity * 360}deg) contrast(${1 + glitchIntensity})`
        }}
      />
      
      {/* RGB Shift - DRAMATIC! */}
      <div 
        className="rgb-shift"
        style={{
          transform: `translateX(${glitchIntensity * 6}px) skewX(${glitchIntensity * 3}deg)`,
          opacity: 0.6 + glitchIntensity * 0.3
        }}
      />
      
      {/* NEW: Digital corruption blocks */}
      <div 
        className="digital-corruption"
        style={{
          opacity: glitchIntensity * 0.8,
          transform: `translateY(${Math.sin(Date.now() * 0.01) * 5}px)`
        }}
      />
      
      {/* NEW: Screen flicker */}
      <div className="screen-flicker" />
      
      {/* PERFECT SPARKLES - Don't change these! */}
      <div className="floating-diamonds">
        {diamonds.map((diamond) => (
          <div
            key={diamond.id}
            className={`diamond-cross diamond-type-${diamond.type}`}
            style={{
              left: `${diamond.x}%`,
              top: `${diamond.y}%`,
              transform: `scale(${diamond.size})`,
              animationDelay: `${diamond.delay}s`,
              animationDuration: `${diamond.duration}s`,
            }}
          >
            <span className="diamond-symbol"></span>
          </div>
        ))}
      </div>
      
      {/* Enhanced CRT Vignette */}
      <div className="crt-vignette" />
    </div>
  );
};

export default GlitchOverlay;
