import React, { useState, useRef, useEffect, useCallback } from 'react';
import { motion } from 'framer-motion';
import './Projects.css';
import projectsData from '../data/projects';
import ProjectWindow from './ProjectWindow';
import { soundManager } from '../utils/soundManager';

function Projects() {
  const [filter, setFilter] = useState('all');
  const [selectedProject, setSelectedProject] = useState(null);
  const scrollRef = useRef(null);
  const hideTimeout = useRef(null);

  const filteredProjects = filter === 'all'
    ? projectsData
    : projectsData.filter(project => project.category === filter);

  useEffect(() => {
    const el = scrollRef.current;
    if (!el) return;
    const onWheel = (e) => {
      if (e.deltaY !== 0) {
        e.preventDefault();
        el.scrollLeft += e.deltaY;
      }
    };
    el.addEventListener('wheel', onWheel, { passive: false });
    return () => el.removeEventListener('wheel', onWheel);
  }, [filteredProjects.length, filter]);

  const handleMouseMove = useCallback((e) => {
    if (!scrollRef.current) return;
    const rect = scrollRef.current.getBoundingClientRect();
    const nearScrollbar = e.clientY > rect.bottom - 20;

    if (hideTimeout.current) {
      clearTimeout(hideTimeout.current);
      hideTimeout.current = null;
    }

    if (nearScrollbar) {
      hideTimeout.current = setTimeout(() => {
        window.dispatchEvent(new CustomEvent('scrollbarHoverEnter'));
      }, 300);
    } else {
      window.dispatchEvent(new CustomEvent('scrollbarHoverLeave'));
    }
  }, []);

  const handleMouseLeave = () => {
    if (hideTimeout.current) {
      clearTimeout(hideTimeout.current);
      hideTimeout.current = null;
    }
    window.dispatchEvent(new CustomEvent('scrollbarHoverLeave'));
  };

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: { opacity: 1, transition: { staggerChildren: 0.1 } }
  };
  
  const cardVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.5 } }
  };

  const openProjectWindow = (project) => {
    soundManager.play('windowOpen');
    setTimeout(() => {
      setSelectedProject(project);
      }, 50);
  };

  const closeProjectWindow = () => {
    setSelectedProject(null);
  };

  const handleFilterClick = (filterValue) => {
    soundManager.play('filterSelect');
    setFilter(filterValue);
  };

  return (
    <section className="projects-section">
      <div className="projects-container">
        <motion.h2
          className="projects-title"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          Stuff I Built Because I Could:
        </motion.h2>

        <div className="filter-buttons">
          <button
            className={`filter-btn ${filter === 'all' ? 'active' : ''}`}
            onClick={() => handleFilterClick('all')}
          >All</button>
          <button
            className={`filter-btn ${filter === 'professional' ? 'active' : ''}`}
            onClick={() => handleFilterClick('professional')}
          >Professional</button>
          <button
            className={`filter-btn ${filter === 'personal' ? 'active' : ''}`}
            onClick={() => handleFilterClick('personal')}
          >Personal</button>
        </div>

        <ProjectWindow
          project={selectedProject}
          isOpen={!!selectedProject}
          onClose={closeProjectWindow}
        />

        <motion.div
          key={filter}
          ref={scrollRef}
          className="projects-grid"
          variants={containerVariants}
          initial="hidden"
          animate="visible"
          onMouseMove={handleMouseMove}
          onMouseLeave={handleMouseLeave}
        >
          {filteredProjects.map((project) => (
            <motion.div
              key={project.id}
              className="project-card"
              variants={cardVariants}
              onClick={() => openProjectWindow(project)}
              tabIndex={0}
              role="button"
              aria-pressed="false"
            >
              <div className="project-image-container">
                <img
                  src={project.image}
                  alt={project.title}
                  className="project-image"
                />
                <div className="project-category-badge">
                  {project.category}
                </div>
              </div>

              <div className="project-content">
                <h3 className="project-title">{project.title}</h3>
              </div>
            </motion.div>
          ))}
        </motion.div>

        {filteredProjects.length === 0 && (
          <motion.div
            className="no-projects"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
          >
            <p>No projects found in this category.</p>
          </motion.div>
        )}
      </div>
    </section>
  );
}

export default Projects;