import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ReactTyped } from 'react-typed';
import avatarIdle from '../assets/images/me-pixel.png';
import avatarJump from '../assets/images/me-pixel-jump.png';
import './Avatar8bit.css';

function Avatar8bit() {
  const [animationPhase, setAnimationPhase] = useState('jumping');
  const [showBubble, setShowBubble] = useState(false);
  const [startTyping, setStartTyping] = useState(false);
  const [typedDone, setTypedDone] = useState(false);
  const [canDismiss, setCanDismiss] = useState(false); //  NEW: Controls when user can click

  useEffect(() => {
    const jumpTimer = setTimeout(() => {
      setAnimationPhase('landing');
    }, 1800);

    const idleTimer = setTimeout(() => {
      setAnimationPhase('idle');
    }, 2400);

    // const bubbleTimer = setTimeout(() => {
    //   setShowBubble(true);
    //   // Start typing after bubble scales in
    //   setTimeout(() => setStartTyping(true), 400);
    // }, 8000);

    return () => {
      clearTimeout(jumpTimer);
      clearTimeout(idleTimer);
      // clearTimeout(bubbleTimer);
    };
  }, []);

  //  NEW: Enable clicking after all animations complete
  useEffect(() => {
    if (typedDone) {
      const enableClickTimer = setTimeout(() => {
        setCanDismiss(true);
      }, 800); // Small delay after typing finishes
      return () => clearTimeout(enableClickTimer);
    }
  }, [typedDone]);

  //  NEW: Handle dialog click to dismiss
  const handleDialogClick = () => {
    if (canDismiss) {
      setShowBubble(false);
      setCanDismiss(false);
      setTypedDone(false);
      setStartTyping(false);
    }
  };

  const jumpVariants = {
    initial: { 
      y: '20vh',                    
      x: '-100vw',                  
      scale: 0.8,                   
      rotate: -10                   
    },
    animate: { 
      y: [
        '20vh',    
        '-10vh',   
        '0vh'      
      ],
      x: [
        '-100vw',  
        '-15vw',   
        '0vw'      
      ],
      scale: [
        0.8,       
        1.1,       
        1          
      ],
      rotate: [
        -10,       
        3,         
        0          
      ],
      transition: {
        duration: 2.0,              
        times: [0, 0.4, 1],         
        ease: [0.4, 0, 0.6, 1]      
      }
    }
  };

  const idleVariants = {
    initial: { scale: 0.9, y: '1vh', rotate: 2 },
    animate: { 
      scale: 1, 
      y: 0,
      rotate: 0,
      transition: {
        type: "spring",
        stiffness: 300,      
        damping: 20,
        bounce: 0.3        
      }
    }
  };

  // Epic game-style bubble entrance
  const bubbleVariants = {
    initial: { 
      opacity: 0,
      scale: 0.8,
      y: '2vh'
    },
    animate: { 
      opacity: 1,
      scale: 1,
      y: 0,
      transition: { 
        duration: 0.5,
        ease: [0.68, -0.55, 0.265, 1.55]  // Bouncy game feel
      }
    }
  };

  //  NEW: Pop-out animation when dismissed
  const bubbleExitVariants = {
    opacity: 0,
    scale: 1.1,
    y: '-5px',
    transition: { 
      duration: 0.2,
      ease: "easeOut"
    }
  };

  const finalBubbleText = 'I am open to work!';

  return (
    <div className="avatar-8bit-container">
      <AnimatePresence mode="wait">
        {animationPhase === 'jumping' && (
          <motion.img
            key="jumping"
            src={avatarJump}
            alt="Engel jumping avatar"
            className="avatar-8bit"
            variants={jumpVariants}
            initial="initial"
            animate="animate"
          />
        )}
        
        {(animationPhase === 'landing' || animationPhase === 'idle') && (
          <motion.img
            key="idle"
            src={avatarIdle}
            alt="Engel idle avatar"
            className="avatar-8bit"
            variants={idleVariants}
            initial="initial"
            animate="animate"
          />
        )}
      </AnimatePresence>

      {/*  Interactive Game Dialog */}
      <AnimatePresence>
        {showBubble && (
          <motion.div
            className={`work-bubble ${canDismiss ? 'clickable' : ''}`}
            variants={bubbleVariants}
            initial="initial"
            animate="animate"
            exit={bubbleExitVariants}
            onClick={handleDialogClick}
          >
            {/* Dialog content */}
            {!typedDone && startTyping ? (
              <ReactTyped
                strings={[finalBubbleText]}
                typeSpeed={35}
                backSpeed={0}
                backDelay={0}
                showCursor={false}
                loop={false}
                onComplete={() => setTypedDone(true)}
              />
            ) : typedDone ? (
              <span>{finalBubbleText}</span>
            ) : (
              <span style={{ opacity: 0 }}>{finalBubbleText}</span>
            )}

            {/*  NEW: Blinking indicator - only shows when user can click */}
            {canDismiss && (
              <div className="dialog-indicator"></div>
            )}
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}

export default Avatar8bit;
