// Skills.jsx
import './Skills.css';
import tools from '../data/tools.js';

function Skills() {
  return (
    <section className="skills-section">
      {/* <h2 className="skills-heading">What I'm Capable Of</h2> */}
      <p className="skills-subtext">I don't just use tools, I make them fight for efficiency. Here's my arsenal:</p>
      <div className="skills-grid">
        {tools.map((tool, index) => (
          <div key={index} className="skill-card" style={{ '--i': index }}>

            <img 
              src={`${process.env.PUBLIC_URL}${tool.logo}`} 
              alt={`${tool.name} logo`} 
              className="skill-logo" 
            />

            {/* <span className="skill-name">{tool.name}</span> */}
            <span className="skill-category">{tool.category}</span>
          </div>
        ))}
      </div>
    </section>
  );
}

export default Skills;
