import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import './App.css';
import HeaderNav from './components/HeaderNav';
import MainContent from './components/MainContent';
import Footer from './components/Footer';
import SparkleBackground from './components/SparkleBackground';
import CustomCursor from './components/CustomCursor';
import MobileUnderConstruction from './components/MobileUnderConstruction';
import LoadingScreen from './components/LoadingScreen';
import PreloadManager from './components/PreloadManager';
import { useAnimationController } from './components/AnimationController';

function App() {
  const [activeSection, setActiveSection] = useState('landing');
  const [isMobile, setIsMobile] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [loadingProgress, setLoadingProgress] = useState(0);
  const [ready, setReady] = useState(false);
  const [showUI, setShowUI] = useState(false);
  
  // NEW: Hide custom cursor when over scrollbar
  const [hideCustomCursor, setHideCustomCursor] = useState(false);

  const [preloadManager] = useState(() => new PreloadManager());
  const { state: animState, trigger } = useAnimationController();

  // Console Easter Egg
  useEffect(() => {
    setTimeout(() => {
      console.log(`%c
    Hey there, fellow developer! 
    Thanks for checking out my code 
    Found any interesting bugs? Let me know!
     I'm available for hire: engelgatus@gmail.com  
    
     Try clicking "Engel Gatus" in the footer...
      `, 'color: #4CAF50; font-weight: bold; font-size: 14px;');
    }, 2000);
  }, []);

  // NEW: Listen for scrollbar hover events from Projects
  useEffect(() => {
    const handleScrollbarEnter = () => setHideCustomCursor(true);
    const handleScrollbarLeave = () => setHideCustomCursor(false);

    window.addEventListener('scrollbarHoverEnter', handleScrollbarEnter);
    window.addEventListener('scrollbarHoverLeave', handleScrollbarLeave);

    return () => {
      window.removeEventListener('scrollbarHoverEnter', handleScrollbarEnter);
      window.removeEventListener('scrollbarHoverLeave', handleScrollbarLeave);
    };
  }, []);

  useEffect(() => {
    const checkScreenSize = () => setIsMobile(window.innerWidth <= 768);
    checkScreenSize();
    window.addEventListener('resize', checkScreenSize);
    return () => window.removeEventListener('resize', checkScreenSize);
  }, []);

  useEffect(() => {
    let mounted = true;
    preloadManager.onProgress((p) => mounted && setLoadingProgress(p));
    (async () => {
      try { await preloadManager.start(); } finally { mounted && setIsLoading(false); }
    })();
    return () => { mounted = false; };
  }, [preloadManager]);

  const handleLoadingComplete = () => {
    setReady(true);
    trigger('loadingComplete');
    
    console.log(' App: Loading complete, waiting for first typed completion...');
    
    setTimeout(() => {
      if (!showUI) {
        console.log(' App: Fallback triggered - showing UI after 10s');
        setShowUI(true);
      }
    }, 10000);
  };

  useEffect(() => {
    console.log(' App: Animation state changed:', {
      introTypingComplete: animState.introTypingComplete,
      showUI: showUI
    });
    
    if (animState.introTypingComplete && !showUI) {
      console.log(' App: introTypingComplete detected - showing HeaderNav and Footer!');
      setShowUI(true);
    }
  }, [animState.introTypingComplete, showUI]);

  useEffect(() => {
    console.log(' App: showUI changed to:', showUI);
  }, [showUI]);

  if (isLoading || !ready) {
    return (
      <LoadingScreen
        isLoading={isLoading}
        progress={loadingProgress}
        onComplete={handleLoadingComplete}
      />
    );
  }

  if (isMobile) {
    return <MobileUnderConstruction />;
  }

  return (
    <motion.div 
      className="app-wrapper"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.6, ease: "easeOut" }}
    >
      <CustomCursor hidden={hideCustomCursor} />
      <SparkleBackground />

      <MainContent 
        activeSection={activeSection} 
        setActiveSection={setActiveSection}
        animationState={animState}
        triggerAnimation={trigger}
      />

      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: showUI ? 1 : 0 }}
        transition={{ 
          duration: 1.2,
          ease: [0.23, 1, 0.32, 1],
          delay: 0.05
        }}
        style={{
          pointerEvents: showUI ? 'auto' : 'none'
        }}
      >
        <HeaderNav 
          setActiveSection={setActiveSection} 
          activeSection={activeSection}
        />
      </motion.div>

      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: showUI ? 1 : 0 }}
        transition={{ 
          duration: 0.8,
          ease: "easeOut",
          delay: 0.1
        }}
        style={{
          pointerEvents: showUI ? 'auto' : 'none'
        }}
      >
        <Footer />
      </motion.div>

      <div id="global-overlay" aria-hidden="true" />
    </motion.div>
  );
}

export default App;
