import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';

// Enhanced Error Boundary Component
class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false, error: null, errorInfo: null };
  }

  static getDerivedStateFromError(error) {
    return { hasError: true };
  }

  componentDidCatch(error, errorInfo) {
    console.error('Portfolio Error Boundary caught an error:', error, errorInfo);
    this.setState({
      error: error,
      errorInfo: errorInfo
    });
  }

  render() {
    if (this.state.hasError) {
      return (
        <div style={{
          padding: '2rem',
          textAlign: 'center',
          fontFamily: '"Press Start 2P", monospace',
          backgroundColor: '#1a1a2e',
          color: '#00ff88',
          minHeight: '100vh',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center'
        }}>
          <h1 style={{ fontSize: '1.5rem', marginBottom: '1rem' }}>
            SYSTEM ERROR
          </h1>
          <p style={{ fontSize: '0.75rem', marginBottom: '2rem' }}>
            Portfolio failed to initialize properly.
          </p>
          <button 
            onClick={() => window.location.reload()}
            style={{
              padding: '0.75rem 1.5rem',
              fontSize: '0.625rem',
              backgroundColor: 'transparent',
              border: '2px solid #00ff88',
              color: '#00ff88',
              fontFamily: 'inherit',
              cursor: 'pointer',
              borderRadius: '4px'
            }}
          >
            RESTART SYSTEM
          </button>
          {process.env.NODE_ENV === 'development' && (
            <details style={{ marginTop: '2rem', fontSize: '0.5rem', color: '#666' }}>
              <summary>Debug Information</summary>
              <pre>{this.state.error && this.state.error.toString()}</pre>
              <pre>{this.state.errorInfo.componentStack}</pre>
            </details>
          )}
        </div>
      );
    }

    return this.props.children;
  }
}

// Create root and render app with error boundary
const root = ReactDOM.createRoot(document.getElementById('root'));

// Enhanced rendering with better error handling
const renderApp = () => {
  try {
    root.render(
      <React.StrictMode>
        <ErrorBoundary>
          <App />
        </ErrorBoundary>
      </React.StrictMode>
    );
  } catch (error) {
    console.error('Fatal error during app render:', error);
    
    // Fallback rendering in case of critical failure
    root.render(
      <div style={{
        padding: '2rem',
        textAlign: 'center',
        fontFamily: 'monospace',
        backgroundColor: '#1a1a2e',
        color: '#ff4444',
        minHeight: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <h1>Critical System Failure</h1>
        <p>Please refresh the page to restart the portfolio.</p>
      </div>
    );
  }
};

// Initialize the application
renderApp();

// Optional: Simple performance logging without external dependencies
if (process.env.NODE_ENV === 'development') {
  // Basic performance monitoring
  window.addEventListener('load', () => {
    const perfData = performance.getEntriesByType('navigation')[0];
    if (perfData) {
      console.log('Portfolio Load Performance:', {
        domContentLoaded: Math.round(perfData.domContentLoadedEventEnd - perfData.domContentLoadedEventStart),
        loadComplete: Math.round(perfData.loadEventEnd - perfData.loadEventStart),
        total: Math.round(perfData.loadEventEnd - perfData.fetchStart)
      });
    }
  });
}

// Hot module replacement for development
if (module.hot) {
  module.hot.accept('./App', renderApp);
}
