import { useState, useEffect, useCallback } from 'react';

class AnimationController {
  constructor() {
    this.listeners = new Map();
    this.state = {
      loadingComplete: false,
      avatarAnimationComplete: false,
      introTypingComplete: false,
      firstStringComplete: false,
      navFooterVisible: false,
      sequenceComplete: false
    };
  }

  // Subscribe to animation events
  subscribe(event, callback) {
    if (!this.listeners.has(event)) {
      this.listeners.set(event, new Set());
    }
    this.listeners.get(event).add(callback);


    // Return unsubscribe function
    return () => {
      const eventListeners = this.listeners.get(event);
      if (eventListeners) {
        eventListeners.delete(callback);
      }
    };
  }

  // Emit events to all subscribers
  emit(event, data = null) {
    console.log(` Animation Event: ${event}`, data);
    const eventListeners = this.listeners.get(event);
    if (eventListeners) {
      eventListeners.forEach(callback => callback(data));
    }
  }

  // Update internal state and emit events
  setState(updates) {
    const prevState = { ...this.state };
    this.state = { ...this.state, ...updates };


    // Emit events for state changes
    Object.keys(updates).forEach(key => {
      if (prevState[key] !== this.state[key]) {
        this.emit(`${key}Changed`, this.state[key]);
      }
    });

    // Check for sequence completion
    if (this.state.navFooterVisible && !prevState.sequenceComplete) {
      this.setState({ sequenceComplete: true });
      this.emit('sequenceComplete');
    }
  }

  // Trigger specific animation phases
  startLoadingComplete() {
    this.setState({ loadingComplete: true });
  }

  startAvatarAnimation() {
    this.setState({ avatarAnimationComplete: false });
    // Avatar animation will call completeAvatarAnimation when done
  }

  completeAvatarAnimation() {
    this.setState({ avatarAnimationComplete: true });
  }

  completeIntroTyping() {
    this.setState({ introTypingComplete: true });
    // Trigger nav and footer entrance
    setTimeout(() => {
      this.setState({ navFooterVisible: true });
    }, 300); // Small delay for smooth transition
  }

  // New method for first string completion
  completeFirstString() {
    this.setState({ firstStringComplete: true });
    // Also mark intro typing as complete for backwards compatibility
    this.setState({ introTypingComplete: true });
    // Trigger nav and footer entrance
    setTimeout(() => {
      this.setState({ navFooterVisible: true });
    }, 300); // Small delay for smooth transition
  }

  // Get current state
  getState() {
    return { ...this.state };
  }
}

// Create singleton instance
const animationController = new AnimationController();

// React hook for using the animation controller
export const useAnimationController = () => {
  const [state, setState] = useState(animationController.getState());

  useEffect(() => {
    // Get initial state structure (doesn't need to be reactive)
    const initialState = animationController.getState();
    
    // Subscribe to all state changes
    const unsubscribes = Object.keys(initialState).map(key => 
      animationController.subscribe(`${key}Changed`, (value) => {
        setState(prevState => ({ ...prevState, [key]: value }));
      })
    );


    // Subscribe to sequence complete
    const unsubSequence = animationController.subscribe('sequenceComplete', () => {
      setState(animationController.getState());
    });

    return () => {
      unsubscribes.forEach(unsub => unsub());
      unsubSequence();
    };
  }, []); // Empty dependency array is now correct

  const trigger = useCallback((action, data) => {
    switch (action) {
      case 'loadingComplete':
        animationController.startLoadingComplete();
        break;
      case 'avatarAnimationComplete':
        animationController.completeAvatarAnimation();
        break;
      case 'introTypingComplete':
        animationController.completeIntroTyping();
        break;
      case 'firstStringComplete':
        animationController.completeFirstString();
        break;
      default:
        console.warn(`Unknown animation action: ${action}`);
    }
  }, []);

  return { state, trigger };
};

export default animationController;
