import React, { useEffect, useState } from 'react';
import './MobileUnderConstruction.css';
import thumbsUpAvatar from '../assets/images/me-pixel-thumb.png';

const MobileUnderConstruction = () => {
  const [sparkles, setSparkles] = useState([]);

  // Generate sparkles ONCE with staggered infinite timing
  useEffect(() => {
    const generateSparkles = () => {
      const newSparkles = [];
      for (let i = 0; i < 15; i++) {
        newSparkles.push({
          id: i,
          left: Math.random() * 100,
          top: Math.random() * 100,
          // FIXED: Stagger delays across a longer cycle to prevent synchronized refresh
          delay: (Math.random() * 8) + (i * 0.3), // Spreads delays from 0-11+ seconds
          duration: 2 + Math.random() * 2,
          size: 0.5 + Math.random() * 1.5,
        });
      }
      setSparkles(newSparkles);
    };

    generateSparkles();

    // REMOVED: No more interval! Sparkles now loop infinitely
    // const interval = setInterval(generateSparkles, 5000);
    // return () => clearInterval(interval);
  }, []);

  return (
    <div className="mobile-construction">
      {/* Sparkling Background - Same beautiful animation, no refresh! */}
      <div className="sparkle-container">
        {sparkles.map((sparkle) => (
          <div
            key={sparkle.id}
            className="sparkle"
            style={{
              left: `${sparkle.left}%`,
              top: `${sparkle.top}%`,
              animationDelay: `${sparkle.delay}s`,
              animationDuration: `${sparkle.duration}s`,
              transform: `scale(${sparkle.size})`,
            }}
          >
            {/* Empty - CSS ::before creates the pixel diamond */}
          </div>
        ))}
      </div>

      {/* Main Content */}
      <div className="construction-content">
        {/* Avatar with smooth entrance animation */}
        <div className="avatar-container">
          <img 
            src={thumbsUpAvatar} 
            alt="Thumbs up avatar" 
            className="thumbs-avatar"
          />
          <div className="avatar-glow"></div>
        </div>

        {/* Main Text */}
        <div className="construction-text">
          <h1 className="main-title">Site under construction</h1>
          <div className="subtitle">
            <span className="accent-text">Coming soon to mobile</span>
          </div>
        </div>

        {/* Animated dots for loading effect */}
        <div className="loading-dots">
          <span className="dot"></span>
          <span className="dot"></span>
          <span className="dot"></span>
        </div>

        {/* Optional: Rotate device hint */}
        <div className="rotate-hint">
          <p>For the full experience, try desktop or rotate your device</p>
        </div>
      </div>

      {/* Floating particles for extra magic */}
      <div className="floating-particles">
        {[...Array(8)].map((_, i) => (
          <div 
            key={i} 
            className="particle"
            style={{
              left: `${10 + (i * 10)}%`,
              animationDelay: `${i * 0.5}s`,
              animationDuration: `${3 + (i * 0.3)}s`,
            }}
          ></div>
        ))}
      </div>
    </div>
  );
};

export default MobileUnderConstruction;
