import React, { useState, useEffect, useRef } from 'react';
import { motion } from 'framer-motion';
import NPCDialog from './NPCDialog';
import './HeaderNav.css';
import logo from '../assets/images/me-pixel-head.png';
import { soundManager } from '../utils/soundManager';

function HeaderNav({ setActiveSection, activeSection }) {
  const [menuOpen, setMenuOpen] = useState(false);
  const [npcMessage, setNpcMessage] = useState('');
  const [dialogTrigger, setDialogTrigger] = useState(0);
  const [dismissDialog, setDismissDialog] = useState(0);
  
  // Track dialog timeout for cleanup
  const dialogTimeoutRef = useRef(null);

  useEffect(() => {
    soundManager.init();
  }, []);

  // Cleanup timeout on unmount
  useEffect(() => {
    return () => {
      if (dialogTimeoutRef.current) {
        clearTimeout(dialogTimeoutRef.current);
      }
    };
  }, []);

  const npcMessages = {
    // landing: "Oh, welcome back.",
    about: "Ah, want to know my story? Eh?",
    skills: "Check out these skills I have!",
    projects: "Take a look at what I've built!",
    contact: "Let's build something amazing together!"
  };

  // Framer Motion variants for logo animation
  const logoVariants = {
    hidden: { 
      x: -200,  // Increased distance to reach viewport edge
      opacity: 0,
      pointerEvents: 'none' // Disable clicks when hidden
    },
    visible: { 
      x: 0, 
      opacity: 1,
      pointerEvents: 'auto',
      transition: {
        type: "spring",
        damping: 12,       // Higher = less bounce (more controlled/organic)
        stiffness: 140,    // Higher = snappier/more responsive
        mass: 1.0,         // Lower = less momentum/overshoot
        duration: 0.6
      }
    },
    exit: {
      x: -200,  // Sprint all the way to viewport edge
      opacity: 0,
      pointerEvents: 'none',
      transition: {
        duration: 0.35,
        ease: [0.6, 0.01, 0.05, 0.95] // Fast sprint out (easeInExpo)
      }
    }
  };

  const handleGoBack = () => {
    soundManager.play('navClick');
    setActiveSection('landing');
    setMenuOpen(false);
    
    // Clear any pending dialog
    if (dialogTimeoutRef.current) {
      clearTimeout(dialogTimeoutRef.current);
    }
    
    // Dismiss dialog smoothly as logo exits
    setDismissDialog(prev => prev + 1);
    
    // No dialog for landing page
    // setNpcMessage(npcMessages.landing);
    // setDialogTrigger(prev => prev + 1);
  };

  const handleNavClick = (section) => {
    soundManager.play('navClick');
    setActiveSection(section);
    setMenuOpen(false);
    
    // Clear any pending dialog timeout
    if (dialogTimeoutRef.current) {
      clearTimeout(dialogTimeoutRef.current);
    }
    
    // Delay NPC dialog until after logo settles (600ms matches animation)
    dialogTimeoutRef.current = setTimeout(() => {
      setNpcMessage(npcMessages[section] || "Thanks for clicking! ");
      setDialogTrigger(prev => prev + 1);
    }, 600);
  };

  // Determine which animation state to use
  const getLogoAnimationState = () => {
    if (activeSection === 'landing') {
      return 'hidden';
    }
    return 'visible';
  };

  return (
    <div>
      <nav className="header-nav">
        <motion.div 
          className="logo-container" 
          onClick={handleGoBack}
          variants={logoVariants}
          initial="hidden"
          animate={getLogoAnimationState()}
        >
          <img src={logo} alt="Engel Gatus logo" className="logo" />
        </motion.div>

        <ul className={`nav-links${menuOpen ? ' open' : ''}`}>
          <li onClick={() => handleNavClick('about')}>About</li>
          <li onClick={() => handleNavClick('skills')}>What I am capable of</li>
          <li onClick={() => handleNavClick('projects')}>Projects</li>
          <li onClick={() => handleNavClick('contact')}>Contact</li>
        </ul>

        <button
          className={`burger${menuOpen ? ' open' : ''}`}
          onClick={() => setMenuOpen(o => !o)}
          aria-label="Toggle navigation"
        >
          <span />
          <span />
          <span />
        </button>
      </nav>

      <NPCDialog 
        trigger={dialogTrigger} 
        message={npcMessage}
        dismissTrigger={dismissDialog}
      />
    </div>
  );
}

export default HeaderNav;
