import { Howl } from 'howler';

class SoundManager {
  constructor() {
    this.sounds = {};
    this.enabled = true;
    this.volume = 0.25;
    this.isInitialized = false;
  }

  init() {
    if (this.isInitialized) return;

    this.sounds.windowOpen = new Howl({
      src: [`${process.env.PUBLIC_URL}/sounds/window-open.wav`],
      volume: this.volume,
      preload: true,
      
    });

    this.sounds.windowClose = new Howl({
      src: [`${process.env.PUBLIC_URL}/sounds/window-close.wav`],
      volume: this.volume,
      preload: true
    });

    this.sounds.filterSelect = new Howl({
      src: [`${process.env.PUBLIC_URL}/sounds/filter-select.wav`],
      volume: this.volume,
      preload: true
    });

    this.sounds.navClick = new Howl({
      src: [`${process.env.PUBLIC_URL}/sounds/nav-click.mp3`],
      volume: this.volume,
      preload: true
    });

    this.isInitialized = true;
  }

  play(soundKey) {
    if (!this.enabled || !this.isInitialized) return;
    
    const sound = this.sounds[soundKey];
    if (sound) {
      sound.play();
    }
  }

  setVolume(vol) {
    this.volume = Math.max(0, Math.min(1, vol));
    Object.values(this.sounds).forEach(sound => {
      if (sound) sound.volume(this.volume);
    });
  }

  toggle() {
    this.enabled = !this.enabled;
    return this.enabled;
  }

  getEnabled() {
    return this.enabled;
  }
}

export const soundManager = new SoundManager();