class PreloadManager {
  constructor() {
    this.loadedAssets = 0;
    this.totalAssets = 0;
    this.onProgressCallback = null;

    this.criticalAssets = {
      images: [
        // TODO: replace with real paths or leave empty array
      ],
      fonts: []
    };
  }

  onProgress(cb) {
    this.onProgressCallback = cb;
    return this;
  }

  _emit() {
    const progress = this.totalAssets ? (this.loadedAssets / this.totalAssets) * 100 : 100;
    this.onProgressCallback && this.onProgressCallback(progress, this.loadedAssets, this.totalAssets);
  }

  preloadImage(src) {
    return new Promise((resolve) => {
      const img = new Image();
      img.onload = () => { this.loadedAssets++; this._emit(); resolve(); };
      img.onerror = () => { this.loadedAssets++; this._emit(); resolve(); };
      img.src = src;
    });
  }

  preloadFont(href) {
    return new Promise((resolve) => {
      const link = document.createElement('link');
      link.rel = 'preload';
      link.as = 'style';
      link.href = href;
      link.onload = () => { this.loadedAssets++; this._emit(); resolve(); };
      link.onerror = () => { this.loadedAssets++; this._emit(); resolve(); };
      document.head.appendChild(link);
    });
  }

  initializeCriticalSystems() {
    return new Promise((resolve) => {
      setTimeout(() => { this.loadedAssets++; this._emit(); resolve(); }, 400);
    });
  }

  async preloadAll() {
    this.totalAssets = this.criticalAssets.images.length + this.criticalAssets.fonts.length + 1;
    this.loadedAssets = 0;
    this._emit();

    const tasks = [
      ...this.criticalAssets.images.map((src) => this.preloadImage(src)),
      ...this.criticalAssets.fonts.map((f) => this.preloadFont(f)),
      this.initializeCriticalSystems()
    ];

    // Safety timeout: never hang longer than 6s
    const safety = new Promise((resolve) => setTimeout(resolve, 6000));

    await Promise.race([
      Promise.allSettled(tasks),
      safety
    ]);
  }

  async start() {
    const t0 = Date.now();
    await this.preloadAll();

    const min = 1200;
    const dt = Date.now() - t0;
    if (dt < min) await new Promise((r) => setTimeout(r, min - dt));
    return true;
  }
}

export default PreloadManager;
