import React, { useState, useRef, useEffect } from 'react';

// Custom hook for IntersectionObserver
function useIntersection(ref) {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    if (!ref.current) return;
    const observer = new window.IntersectionObserver(
      ([entry]) => setIsVisible(entry.isIntersecting),
      { threshold: 0.1 }
    );
    observer.observe(ref.current);
    return () => observer.disconnect();
  }, [ref]);

  return isVisible;
}

export function VideoThumbnail({ thumbnailSrc, videoSrc, alt, onHoverChange }) {
  const [playing, setPlaying] = useState(false);
  const containerRef = useRef(null);
  const isVisible = useIntersection(containerRef);

  const handleMouseEnter = () => {
    if (onHoverChange) onHoverChange(true);
  };
  const handleMouseLeave = () => {
    if (onHoverChange) onHoverChange(false);
  };

  return (
    <div
      ref={containerRef}
      style={{ position: 'relative', cursor: 'pointer', maxWidth: '100%', borderRadius: '12px', overflow: 'hidden' }}
      onClick={() => setPlaying(true)}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      aria-label={`Play video: ${alt}`}
      tabIndex={0}
      role="button"
    >
      {/* Lazy-load thumbnail img and video only when visible */}
      {isVisible && (
        !playing ? (
          <>
            <img src={thumbnailSrc} alt={alt} style={{ width: '100%', display: 'block' }} loading="lazy"/>
            <div style={{
              position: 'absolute', top: '50%', left: '50%',
              transform: 'translate(-50%, -50%)',
              pointerEvents: 'none',
              color: 'white',
              fontSize: '3rem',
              textShadow: '0 0 10px rgba(0,0,0,0.6)'
            }}></div>
          </>
        ) : (
          <video
            src={videoSrc}
            controls
            autoPlay
            style={{ width: '100%', display: 'block', cursor: 'pointer' }}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
          />
        )
      )}
    </div>
  );
}

export default VideoThumbnail;
