import React, { useState, useEffect, useRef } from 'react';
import './Footer.css';
import GlitchOverlay from './GlitchOverlay';

function Footer() {
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentColorIndex, setCurrentColorIndex] = useState(0);
  const [audioLoaded, setAudioLoaded] = useState(false);
  const audioRef = useRef(null);

  const equalizerColors = [
    '#FF6B9D', '#FFE55C', '#4CAF50', '#2196F3', '#FF8A65',
    '#BA68C8', '#FFD700', '#FF4444', '#00E676', '#00B8FF',
  ];

  //  NEW: Dispatch glitch mode state to cursor
  useEffect(() => {
    const event = new CustomEvent('glitchModeChange', {
      detail: { isActive: isPlaying }
    });
    window.dispatchEvent(event);
  }, [isPlaying]);

  // Initialize audio
  useEffect(() => {
    const initializeAudio = () => {
      try {
        const audioUrl = process.env.PUBLIC_URL + '/rutgermuller.wav';
        audioRef.current = new Audio(audioUrl);
        audioRef.current.preload = 'auto';
        
        audioRef.current.addEventListener('canplaythrough', () => {
          setAudioLoaded(true);
          console.log(' Audio loaded successfully!');
        });
        
        audioRef.current.addEventListener('ended', () => {
          setIsPlaying(false);
          setCurrentColorIndex(0);
        });
        
        audioRef.current.addEventListener('error', (e) => {
          console.log(' Audio error:', e);
          const fallbackPaths = [
            '/portfolio/rutgermuller.wav',
            '/rutgermuller.wav', 
            './rutgermuller.wav'
          ];
          
          let pathIndex = 0;
          const tryNextPath = () => {
            if (pathIndex < fallbackPaths.length) {
              audioRef.current.src = fallbackPaths[pathIndex];
              audioRef.current.load();
              pathIndex++;
            }
          };
          
          tryNextPath();
        });
        
        audioRef.current.load();
      } catch (error) {
        console.log(' Audio initialization error:', error);
      }
    };

    initializeAudio();
  }, []);

  // Dynamic color cycling
  useEffect(() => {
    let interval;
    if (isPlaying) {
      interval = setInterval(() => {
        setCurrentColorIndex((prevIndex) => 
          (prevIndex + 1) % equalizerColors.length
        );
      }, 150);
    }
    
    return () => {
      if (interval) clearInterval(interval);
    };
  }, [isPlaying, equalizerColors.length]);

  // Enhanced click handler
  const handleNameClick = async () => {
    console.log(' Activating GLITCH MODE!');
    
    try {
      if (!audioRef.current) return;

      if (isPlaying) {
        audioRef.current.pause();
        audioRef.current.currentTime = 0;
        setIsPlaying(false);
        setCurrentColorIndex(0);
        console.log(' GLITCH MODE deactivated');
      } else {
        setIsPlaying(true);
        console.log(' GLITCH MODE activated!');
        
        try {
          audioRef.current.currentTime = 0;
          const playPromise = audioRef.current.play();
          
          if (playPromise !== undefined) {
            await playPromise;
            console.log(' 8-bit party mode ENGAGED!');
          }
        } catch (playError) {
          console.log(' Audio blocked, visual effects only');
        }
      }
    } catch (error) {
      console.log(' Click handler error:', error);
    }
  };

  return (
    <>
      <GlitchOverlay isActive={isPlaying} audioRef={audioRef} />
      
      <footer className="site-footer">
        <p>
           2025{' '}
          <span 
            className="name-easter-egg"
            onClick={handleNameClick}
            style={{
              color: isPlaying ? equalizerColors[currentColorIndex] : 'inherit',
              transition: isPlaying ? 'color 0.1s ease' : 'color 0.3s ease',
              cursor: 'pointer',
              fontWeight: isPlaying ? '700' : '600',
              textShadow: isPlaying ? `0 0 4px ${equalizerColors[currentColorIndex]}40` : 'none'
            }}
            title={audioLoaded ? ' Click for GLITCH MODE!' : 'Loading audio...'}
          >
            Engel Gatus
          </span>
        </p>
        <div className="social-links">
          <a href="https://www.linkedin.com/in/engelgatus/" target="_blank" rel="noopener noreferrer">
            LinkedIn
          </a>
          <a href="https://github.com/engelgatus" target="_blank" rel="noopener noreferrer">
            GitHub
          </a>
        </div>
        <p className="attribution">
          Icons by <a href="https://www.flaticon.com/authors/freepik" target="_blank" rel="noopener noreferrer">Freepik</a> via <a href="https://www.flaticon.com/" target="_blank" rel="noopener noreferrer">Flaticon</a>
        </p>
      </footer>
    </>
  );
}

export default Footer;
