import { useState, useEffect } from 'react';
import { AnimatePresence, motion } from 'framer-motion';
import './NPCDialog.css';

function NPCDialog({ trigger, message, dismissTrigger }) {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    if (trigger > 0) { // Only trigger if greater than 0
      setIsVisible(true);

      // Auto-hide after 3 seconds
      const hideTimer = setTimeout(() => {
        setIsVisible(false);
      }, 3000);

      return () => clearTimeout(hideTimer);
    }
  }, [trigger]);

  // Manual dismiss when logo exits
  useEffect(() => {
    if (dismissTrigger > 0) {
      setIsVisible(false);
    }
  }, [dismissTrigger]);

  const dialogVariants = {
    hidden: { 
      opacity: 0, 
      scale: 0.8, 
      y: 10,
      x: -20
    },
    visible: { 
      opacity: 1, 
      scale: 1, 
      y: 0,
      x: 0,
      transition: {
        duration: 0.3,
        ease: [0.68, -0.55, 0.265, 1.55] // Bouncy entrance
      }
    },
    exit: { 
      opacity: 0, 
      scale: 0.9,
      y: -5,
      transition: {
        duration: 0.3,  // Quick fade out on dismiss (was 1.2s)
        ease: "easeOut"
      }
    }
  };

  return (
    <AnimatePresence>
      {isVisible && (
        <motion.div 
          className="npc-dialog"
          variants={dialogVariants}
          initial="hidden"
          animate="visible"
          exit="exit"
        >
          <div className="npc-dialog-content">
            {message}
          </div>
          <div className="npc-dialog-tail"></div>
        </motion.div>
      )}
    </AnimatePresence>
  );
}

export default NPCDialog;
